<template>
  <v-card class="overflow-hidden">
    <v-app-bar
      fixed
      color="black"
      dark
    >
      <v-app-bar-nav-icon @click="toggleNavigationDrawer"></v-app-bar-nav-icon>
      <v-toolbar-title>{{ pageCmpt }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-container>
      <div class="mt-12">
        <slot></slot>
      </div>
    </v-container>
    <navigation-drawer ref="navigationDrawer" />
  </v-card>
</template>

<script>
import NavigationDrawer from './NavigationDrawer'
export default {
  components: { NavigationDrawer },
  computed: {
    pageCmpt () {
      return this.$route.meta.pageName || 'Meu Barbeiro'
    }
  },
  methods: {
    toggleNavigationDrawer () {
      this.$refs.navigationDrawer.toggleNavigationDrawer()
    }
  }
}
</script>